.Header {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 1em;
  margin-bottom: 2em;
  background-color: rgb(138, 43, 2);
  color: #fff;
}

.Subtitle {
  color: rgba(255, 255, 255, 0.858);
  margin-bottom: 20px;
}

.nav-link {
  display: inline-block;
  padding: 0.5em 1em;
  color: rgba(255, 255, 255, 0.835);
  font-weight: bold;
  text-decoration: none;
}

.nav-link-active {
  color: #fff;
  border-bottom: 2px solid;
}

h1{
  margin-bottom: 5px;
}